<template>
  <div class="overlay" :class="overlayClasses" @click="closeSidebar"></div>
  <aside class="aside">
    <img class="aside__img" :src="avatarSrc" alt="">
    <h2 class="aside__name">
      {{ name }}
    </h2>
    <p class="aside__description">
      {{ description }}
    </p>
    <sidebar-info-list v-for="(infoItem, index) in infoItems" 
      :data="infoItem" 
      :caption="infoItem.caption" 
      :type="infoItem.type" 
      :elements="infoItem.elements" 
      :key="index"
    />
  </aside>
</template>

<script>
import SidebarInfoList from './SidebarInfoList.vue';

export default {
  props: {
    isMobileSidebarOpen: {
      type: Boolean,
      required: false,
      default: false,
    },
    avatarSrc: {
      type: String,
      required: true,
    },
    name: {
      type: String,
      required: true,
    },
    description: {
      type: String,
      required: false,
      default: ''
    },
    infoItems: {
      type: Array,
      required: false,
      default() {
        return []
      },
    },
  },
  emits: ['closeSidebar'],
  components: { SidebarInfoList },
  data() {
    return {
      data: this.contacts
    }
  },
  computed: {
    isSidebarOpen() {
      return this.isMobileSidebarOpen
    },
    overlayClasses() {
      return {
        ['is-sidebar-open']: this.isSidebarOpen
      }
    }
  },
  methods: {
    closeSidebar() {
      this.$emit('closeSidebar');
    }
  }
}
</script>

